import { Link } from 'react-router'
import {
  openQrScanner,
  closeQrScanner,
  isQrScannerOpened,
} from '@telegram-apps/sdk'

import { Button } from 'antd'

function ScanQR() {
  async function openScaner() {
    if (openQrScanner.isAvailable()) {
      isQrScannerOpened() // false
      const promise = openQrScanner({
        text: 'Scan the QR',
        onCaptured(qr: any) {
          if (qr === 'qr-content-we-expect') {
            closeQrScanner()
          }
        },
      })
      isQrScannerOpened() // true
      await promise
      isQrScannerOpened() // true
    } else {
      alert('Scaner not ready!')
    }
  }

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        //   flex: 1,
        width: '100%',
        height: '100vh',
        justifyContent: 'center',
        alignItems: 'center',
        //   background: 'red',
        color: 'black',
      }}
    >
      <Link to="/">Home</Link>
      <Button
        type="primary"
        onClick={() => {
          openScaner()
        }}
      >
        Open Scaner
      </Button>
    </div>
  )
}

export default ScanQR
