import WebApp from '@twa-dev/sdk'
import { Link } from 'react-router'
import Post from './Post'

function Home() {
  const startapp_param = WebApp.initDataUnsafe.start_param
  if (startapp_param && startapp_param.startsWith('openPacket')) {
    return <Post />
  } else {
    return (
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          //   flex: 1,
          width: '100%',
          height: '100vh',
          justifyContent: 'center',
          alignItems: 'center',
          //   background: 'red',
          color: 'black',
        }}
      >
        <Link to="/post">Post</Link>
        <Link to="/scan">Scan QR test </Link>
      </div>
    )
  }
}

export default Home
